<template>
  <AuthContainer
    class="admin-reset-password"
  >
    <component
      :is="component"
      @submit="onSubmit"
    />
  </AuthContainer>
</template>

<script>
import { defineAsyncComponent, ref } from 'vue'
import AuthContainer from '@/admin/views/Auth/AuthContainer'

export default {
  name: 'ResetPassword',
  components: {
    AuthContainer,
    ResetPasswordForm: defineAsyncComponent(() => import('@/admin/views/Auth/ResetPassword/ResetPasswordForm')),
    ResetPasswordSuccess: defineAsyncComponent(() => import('@/admin/views/Auth/ResetPassword/ResetPasswordSuccess'))
  },
  setup () {
    const component = ref('ResetPasswordForm')

    const onSubmit = () => {
      component.value = 'ResetPasswordSuccess'
    }

    return {
      component,
      onSubmit
    }
  }
}
</script>

